.mart {
    .container {
        padding-bottom: 250px;
    }
}

.mart-name {
    display: flex;
    align-items: center;
    gap: 20px;
    padding-bottom: 100px;

    .nice-select {
        border: 1px solid $stroke;
        border-radius: 50px;
        color: $titles;
        background: $system;
        height: 72px;
        font-size: 24px;
        font-weight: 600;
        display: flex;
        align-items: center;
        justify-content: center;
        max-width: 215px;
    }

    h2 {
        font-weight: 500;
    }
}
.mart-title {
    width: 100%;
    font-size: 20px;
    
    .mart-top {
        font-weight: 400;
        
        td {
            padding-left: 40px;
            padding-bottom: 30px;
        }
    }

    .mart-item{
        font-weight: 600;

        &-title {
            padding-right: 10px;
            color: $titles;
        }

        .spread-green {
            color: $accent;
        }

        .spread-red {
            color: $errors;
        }

        .mart-trade {
            display: flex;
            justify-content: center;
            width: 124px;
            height: 47px;
            padding: 8px 0px;
            font-size: 18px;
            color: $titles;
            text-decoration: none;
            border: 1px solid $stroke;
            border-radius: 5px;

            &:hover {
                color: $accent;
            }
        }

        td{ 
            
            padding: 32.5px 0 32.5px 40px;
    
            img {
                padding: 0 12px 5px 0;
            }
            
        }
    }
}

.mart-item:nth-child(even){
    background-color: $system;
}

.mart-mb {
    display: none;
}