.banner {
    padding-top: 40px;
    overflow: hidden;
    padding-bottom: 460px;

    h1 {
        margin-bottom: 30px;
    }

    h5 {
        font-weight: 400;
        color: $body-text;
        line-height: 36px;
    }
    .container {
        display: flex;
        justify-content: space-between;
    }

    + section {
        margin-top: -210px;
        transition: 1s;
    }

    &-text {
        max-width: 705px;
        padding-bottom: 80px;
    }

    &-advatages {
        max-width: 790px;
        gap: 40px 50px;
        display: grid;
        grid-template-columns: 1fr 1fr;
        padding-bottom: 80px;

        &-item {
            display: flex;
            align-items: center;
            color: $titles;
            font-size: 18px;
            font-weight: 600;
            gap: 20px;

            .icon {
                border-radius: 8px;
                padding: 8px 8px;
                width: 50px;
                height: 50px;
                display: flex;
                justify-content: center;
                align-items: center;
                background-color: #141810;
                border: none
            }
        }

    }

    &-link {
        font-size: 18px;
        font-weight: 600; 

        svg {
            width: 10px;
            height: 16px;
            margin-left: 5px;
        }

        &:hover {
            svg {
                transform: rotate(90deg);
            }
        }
    }

    .banner-col  {
        position: relative;
    }
}

.form-light {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    z-index: -1;
}

