.form-exchange {
    max-width: 540px;
    padding: 40px;
    border: 1px solid $stroke;
    border-radius: 20px;
    position: relative;
    background-color: $background;

    input {
        border: none ;
        outline: none;
    }

    .input-row{
        background-color: $system;
        border-radius: 12px;
        color: $titles;
        font-size: 24px;
        padding: 36px;
        width: 100%;
        border: 1px solid $stroke;
        align-items: center;

        input {
            padding: 0;
        }
    }

    label {
        padding-bottom: 20px;
    }

    .give {
        padding-bottom: 30px;
    }
    .btn-lager {
        width: 100%;
        padding: 45px 0;
        font-size: 24px;
    }
    
    .nice-select {
        
        .current {
            background-position: left center;
            background-repeat: no-repeat;
            padding-left: 36px;
            background-size: 24px;
            line-height: 24px;
            display: block;
        }
    }


}
    

.exchange-info {
    padding: 12px 0 40px 0;
    display: flex;
    list-style: none;
    flex-wrap: wrap;
    gap: 24px;
    &-item {
        width: calc(50% - 24px);
    }

    &__title{
        font-size: 20px;
        padding: 0 20px 0 0;
    }

    &__value {
        font-size: 20px;
        color: $accent;
    }

}

.nice-select {
    border: 1px solid $stroke;
    background: $system;
    padding: 16px 20px;
    height: 56px;
    font-size: 18px;
    font-weight: 600;
    display: flex;
    align-items: center ;
    min-width: 201px;
}

.nice-select {
    .nice-select-dropdown {
        background: $system;
        
        .nice-select-search-box {
            // display: none;
            padding: 17px 20px;

           input {
            background: $system;
           }
        }
    }

    .list {
        @extend .scroll;
    }

   

    .nice-select-search {
        .list {
            margin: 0;
        }
    }

    .option:hover,
    .ooption:focus,
    .option.selected.focus {
        background: $accent-hover-primary;
    }

    .option {
        display: flex;
        gap: 12px;
        color: $titles;
        font-size: 18px;
        font-weight: 600;
        padding: 12px 20px;
        position: relative;
        &::before {
            content: "";
            position: absolute;
            left: 24px;
            right: 24px;
            top: 0;
            border: 1px solid $stroke;
        }

        &:first-child {
            &::before {
                display: none;
            }
        }
    }
}
