@media screen and (max-width: 767px) {
.container{
    display: none;
}
.mart-mb{
    display: block;
    padding:  20px;
    padding-bottom: 100px;

    &-name {
        padding-bottom: 60px;
        .nice-select {
            border: 1px solid $stroke;
            border-radius: 50px;
            color: $titles;
            background: $system;
            height: 24px;
            font-size: 14px;
            font-weight: 600;
            display: flex;
            align-items: center;
            justify-content: center;
            min-width: 137px;
            padding: 23px 48.5px;
            
        }

        H2{
            font-size: 32px;
            list-style: 40px;
        }
    }
}

.mb-title {
    width: 100%;
    font-size: 14px;
    font-weight: 400;

    .mb-item {

        &-title {
            color: $titles;
        }

        td {
            padding: 21px 0;

        }
        
        .spread-red {
            padding: 5px;
            color: $errors;
            background-color: #260404;
        }

        .spread-green {
            padding: 5px;
            color: $accent;
            background-color: #182709;
        }
    }
    td:nth-child(1) {
        padding-right: 90px;
    }
    td:nth-child(2) {
        text-align: end;
    }

    td:nth-child(3) {
            padding-left: 15px;
    
    }
}


   

}
